*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Original Surfer', cursive;
}

input, button {
    background: none;
    border: none;
    outline: none;
}

.App {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(207, 205, 205);
}

form {
    display: block;
    position: relative;
}

form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: 1; 
    background-image: linear-gradient(to bottom right, #FFCE00, #FE4880);
}

form .form-inner {
    position: relative;
    display: block;
    background-color: #fff;
    padding: 30px;
    z-index: 2
}

form .form-inner h2 {
    color: #888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

form .form-inner .form-group {
    display: block;
    color: #666;
    width: 300px;
    margin-bottom: 15px;
}

.form-inner .form-group label{
    display: block;
    color: #666;
    font-size: 15px;
    margin-bottom: 5px;
    transition: 0.4s;
}

.form-group p {
    margin-top: 40px;
}

.form-inner .form-group:focus-within label {
    color: #FE4880;
}

form .form-inner .form-group input {
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #F8F8F8;
    border-radius: 8px;
    transition: 0.4s;
}

form .form-inner .form-group input:focus {
    box-shadow: 0 0 3px rdba(0, 0, 0, 0.2);
}

form .form-inner input[type="submit"], 
.welcome button {
    display: inline-block;
    font-size: 15px;
    padding: 10px 15px;
    border-radius: 8px;
    background-image: linear-gradient(to left, #FFCE00 50%, #FFCE00 50%, #FE4880);
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color: #fff;
    cursor: pointer;
}
form .form-inner input[type="submit"]:hover, 
.welcome button:hover {
    background-position: 100%;
}

.welcome {
    width: 100%;
    max-width: 480px;
    background-color: #fff;
    padding: 15px 30px;
}

.welcome h2 {
    color: #888;
    font-size: 28px;
    margin-bottom: 30px;
}

.welcome h2 span {
    color: #FE4880;
}

.error {
    font-size: 20px;
    padding-bottom: 10px;
    color: #FE4880;
}

.checkbox {
    text-align: left;
    margin: 0 0 10px;
    display: flex;
    align-items: center;
    gap: 5px;
    width: 300px;
}

.link {
    margin: 10px;
}